import { trigger, state, transition, style, animate } from '@angular/animations';
// exemple
export const slideDownAnimation = trigger('dropdownAnim', [
    state('show', style({
        height: '*'
    })),
    state('hide', style({
        height: 0
    })),
    transition('show <=> hide', animate('.25s ease'))
]);
// blockly
export const alertAnimation = trigger('alertAnim', [
    state('show', style({
        width: '*',
        right: 0,
        opacity:0.85,
        display:'block'
    })),
    state('hide', style({
        width: 0,
        right : -350,
        opacity : 0,
        display: ' none'
    })),
    transition('show <=> hide', animate('.3s ease'))
]);
// setting doc

export const setting_docAnimation = trigger('settin_doc', [
    state('show', style({
        height: '*'
    })),
    state('hide', style({
        height: 0
    })),
    transition('show <=> hide', animate('.25s ease'))
]);
// setting 
export const setting_imgAnimation = trigger('settin_img', [
    state('show', style({
        // height: '*'
        transform:'rotate(0deg)'
    })),
    state('hide', style({
        transform:'rotate(-90deg)'
    })),
    transition('show <=> hide', animate('.2s ease'))
]);

