import { NgModule } from '@angular/core';
import { SharedModule } from '../shared/shared.module';
import { QRCodeModule } from 'angularx-qrcode';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { UpdataComponent } from './updata/updata';
import { SettingPanelComponent } from './setting-panel/setting-panel.component';
import { ResourceManagerComponent } from './resource-manager/resource-manager.component';
import { Message } from './message/message';
@NgModule({
  imports: [SharedModule, QRCodeModule],
  declarations: [Message,HeaderComponent, FooterComponent,UpdataComponent, SettingPanelComponent, ResourceManagerComponent],
  exports: [HeaderComponent, FooterComponent],
  entryComponents: [SettingPanelComponent, ResourceManagerComponent,UpdataComponent]
})
export class CoreModule { }
