import { Component, OnInit } from '@angular/core';
import { GlobalOperatorService } from 'src/app/services/global-operator.service';
import { StoreService } from 'src/app/services/store.service';
import { ZrenderHelperService } from 'src/app/services/zrender-helper.service';
import {
  UnitTrack,
  UnitAngle,
  UnitEarth,
  UnitEnv,
  UnitJoystick,
  UnitWeigh,
  UnitServo,
  UnitPulse,
  UnitMakey,
  UnitLight,
  UnitNeopixel,
  UnitPIR,
  BaseUnit,
  Unit396,
  UnitButton,
  UnitColor,
  UnitDac,
  UnitDualButton,
  UnitIR,
  UnitHub,
  UnitM5Camera,
  UnitNicr,
  UnitProto,
  UnitRelay,
  UnitRGB,
  UnitTOF,
  UnitThermal,
  UnitADC,
  UnitExtio,
  UnitFinger,
  UnitRfid,
  UnitLidarCar,
  UnitCardkb,
} from '../../domain/unit';
import { PORT, UNIT_TYPE } from 'src/app/utils/common-data';
import { CodeEditorService } from 'src/app/services/code-editor.service';
import { timingSafeEqual } from 'crypto';
import { ZrenderHelperMbService } from 'src/app/services/zrender-helper-mb.service';

/**
 * Unit
 */
const createUnitCategory = () => {
  return [
    new UnitEnv({ name: '', port: { port: PORT.A, default: ['A', 'C'] } }),
    new UnitPIR({ name: '', port: { port: PORT.B, default: ['A', 'B', 'C'] } }),
    new UnitNeopixel({ name: '', port: { port: PORT.A, default: ['A', 'B', 'C'] }, count: 10 }),
    new UnitJoystick({ name: '', port: { port: PORT.A, default: ['A', 'C'] } }),

    new UnitMakey({ name: '', port: { port: PORT.A, default: ['A', 'C'] } }),
    new UnitServo({ name: '', port: { port: PORT.A, default: ['A', 'B', 'C'] } }),
    new UnitWeigh({ name: '', port: { port: PORT.A, default: ['A', 'B', 'C'] } }),
    new UnitTrack({ name: '', port: { port: PORT.A, default: ['A', 'C'] } }),
    new UnitButton({ name: '', port: { port: PORT.B, default: ['A', 'B', 'C'] } }),
    new UnitDualButton({ name: '', port: { port: PORT.A, default: ['A', 'B', 'C'] } }),
    new UnitRGB({ name: '', port: { port: PORT.B, default: ['A', 'B', 'C'] } }),
    new UnitRelay({ name: '', port: { port: PORT.B, default: ['A', 'B', 'C'] } }),
    new UnitADC({ name: '', port: { port: PORT.A, default: ['A', 'C'] } }),
    new UnitDac({ name: '', port: { port: PORT.A, default: ['A', 'C'] } }),
    new UnitNicr({ name: '', port: { port: PORT.A, default: ['A', 'C'] } }),
    new UnitIR({ name: '', port: { port: PORT.B, default: ['A', 'B', 'C'] } }),
    new UnitExtio({ name: '', port: { port: PORT.A, default: ['A', 'C'] } }),
    new UnitAngle({ name: '', port: { port: PORT.B, default: ['B'] } }),
    new UnitLight({ name: '', port: { port: PORT.B, default: ['A', 'B', 'C'] } }),
    new UnitEarth({ name: '', port: { port: PORT.B, default: ['A', 'B', 'C'] } }),

    new UnitTOF({ name: '', port: { port: PORT.A, default: ['A', 'C'] } }),
    new UnitColor({ name: '', port: { port: PORT.A, default: ['A', 'C'] } }),

    new UnitRfid({ name: '', port: { port: PORT.A, default: ['A', 'C'] } }),
    new UnitFinger({ name: '', port: { port: PORT.C, default: ['A', 'C'] } }),

    new UnitCardkb({ name: '', port: { port: PORT.A, default: ['A', 'C'] } }),

    new UnitPulse({ name: '', port: { port: PORT.A, default: ['A', 'B', 'C'] } }),

    // new Unit396({ name: '', port: { port: PORT.A, default: ['A','B','C'] }  }),
    // new UnitHub({ name: '', port: { port: PORT.A, default: ['A','B','C'] }  }),
    // new UnitM5Camera({ name: '', port: { port: PORT.B, default: ['A','B','C'] }  }),
    // new UnitProto({ name: '', port: { port: PORT.A, default: ['A','B','C'] }  }),
    new UnitThermal({ name: '', port: { port: PORT.A, default: ['A', 'B', 'C'] } }),
  ];
}

@Component({
  selector: 'app-unit-modal',
  templateUrl: './unit-modal.component.html',
  styleUrls: ['./unit-modal.component.scss']
})
export class UnitModalComponent implements OnInit {

  /** Unit */
  category: BaseUnit[] = [];

  /**  */
  maskModal = null;

  /** Unit */
  selectedUnit = null;


  selectMoreUnit = [];

  Allunit = {};
  isstick;
  constructor(
    private globalOperatorSrv: GlobalOperatorService,
    private storeSrv: StoreService,
    private zrenderHelperSrv: ZrenderHelperService,
    private zrenderHelperSrvmb: ZrenderHelperMbService,
    private codeEditorSrv: CodeEditorService
  ) { }

  ngOnInit() {
    this.storeSrv.getType().subscribe(res => {
      this.isstick = res;
      this.category = createUnitCategory();
      for (let i = 0; i < this.category.length; i++) {
        this.category[i].port = (res == "core") ? this.category[i].port : 'A';
        this.category[i].default = (res == "core") ? this.category[i].default : ['A'];
      }
    });
  }

  // close
  cancel() {
    this.globalOperatorSrv.maskModal.instance.removeMask();
  }

  createUnit(type) {
    // let type = this.selectedUnit.type;
    switch (type) {
      case UNIT_TYPE.ENV:
        return new UnitEnv({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.ANGLE:
        return new UnitAngle({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.PIR:
        return new UnitPIR({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'C'] } });
      case UNIT_TYPE.NEOPIXEL:
        return new UnitNeopixel({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] }, count: this.selectedUnit.count });
      case UNIT_TYPE.JOYSTICK:
        return new UnitJoystick({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.LIGHT:
        return new UnitLight({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.EARTH:
        return new UnitEarth({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.MAKEY:
        return new UnitMakey({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.SERVO:
        return new UnitServo({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.WEIGH:
        return new UnitWeigh({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.PULSE:
        return new UnitPulse({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.TRACK:
        return new UnitTrack({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'C'] } });
      case UNIT_TYPE.BUTTON:
        return new UnitButton({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.DUALBUTTON:
        return new UnitDualButton({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.RGB:
        return new UnitRGB({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.RELAY:
        return new UnitRelay({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.DAC:
        return new UnitDac({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.ADC:
        return new UnitADC({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.TOF:
        return new UnitTOF({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.NCIR:
        return new UnitNicr({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.IR:
        return new UnitIR({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.EXTIO:
        return new UnitExtio({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.COLOR:
        return new UnitColor({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.FINGER:
        return new UnitFinger({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.RFID:
        return new UnitRfid({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
      case UNIT_TYPE.CARDKB:
        return new UnitCardkb({ name: this.storeSrv.getUnitDefauleName(type), port: { port: this.selectedUnit.port, default: this.selectedUnit.default || ['A', 'B', 'C'] } });
    }
  }

  handle(e) {

    let _val = e.target.value;

    if (_val in this.Allunit) {
      delete this.Allunit[e.target.value];
    } else {
      this.Allunit[_val] = this.createUnit(_val);
    }
  }
  ok() {
    for (let i in this.Allunit) {
      this.storeSrv.addUnit(this.Allunit[i]);
    }
    // this.selectMoreUnit.forEach( e => {
    // this.storeSrv.addUnit(this.createUnit(e));
    // });
    this.globalOperatorSrv.maskModal.instance.removeMask();
    if (this.globalOperatorSrv.ismb) {
      this.zrenderHelperSrvmb.updateUnitSvg();
    } else {
      this.zrenderHelperSrv.updateUnitSvg();
    }

    if (!this.globalOperatorSrv.codeLock.value) {
      this.codeEditorSrv.updateWorkspaceValue();
    }
  }
  // xuanz
  selectUnit(unit) {
    // console.log(unit)
    this.selectedUnit = unit;
  }

  stopPropagation(ev: MouseEvent) {
    ev.stopPropagation();
    ev.preventDefault();
  }
  // port
  setUnit(type, ev, key) {
    this.Allunit[type][key] = ev.target.value;
  }
  toDocs(unit, e) {
    e.stopPropagation();
    window.open(`https://docs.m5stack.com/#/en/unit/${unit.type}`, '_blank');
    // e.preventDefault();
  }
}
