interface IToolItem {
    /**  */
    icon: string;

    /**  */
    builder: any;
}

/**
 * 
 */
export class ToolItem implements IToolItem {
    
    icon = '';

    builder = null;

    constructor(obj: IToolItem) {
        this.icon = obj.icon;
        this.builder = obj.builder;
    }

}

/**
 * 
 */
export class ToolBar {

    /**  */
    toolList: ToolItem[] = [];

    constructor(toolList: ToolItem[]) {
        this.toolList = toolList;
    }

}