import { Component, OnInit, ViewChild, ElementRef, HostListener, Console, Renderer2, Directive } from '@angular/core';
import { LANG } from 'src/app/utils/common-data';
import { alertAnimation } from '../../animations';
import { BlocklyEditorService } from 'src/app/services/blockly-editor.service';
// import { footerService } from 'src/app/services/blockly-editor.service';
@Component({
  selector: 'app-message',
  templateUrl: './message.html',
  styleUrls: ['./message.scss'],
  animations: [alertAnimation]
})
@Directive({
  selector: '[appHighlight]'
})
export class Message implements OnInit {
  Alert_Blockly = '122';
  exampleVisibility = false;
  constructor(
    public service: BlocklyEditorService,
    private el: ElementRef,
    private renderer2: Renderer2
  ) { }
  languageList = LANG;
  show: boolean;
  datetime: false;

  ngOnInit() {
    this.Alert_Blockly = this.service.blockly_alert;
    var _this = this;
    var t;
    var t1;
    this.service.getAlert().subscribe({
      next: (v) => {
        if (v && _this.Alert_Blockly !== v) {
          _this.Alert_Blockly = v;
          if (this.exampleVisibility) {
            this.exampleVisibility = false;
            if (t1) {
              // clearInterval(t);
              clearTimeout(t)   // 
            }
            t1 = setTimeout(() => {
              this.exampleVisibility = true;
            }, 300);
          } else {
            this.exampleVisibility = true;
          }
          if (t) {
            // clearInterval(t);
            clearTimeout(t)   // 
          }
          t = setTimeout(() => {
            this.exampleVisibility = false;
          }, 4000);

        } else {
          _this.Alert_Blockly = v;
          return false;
        }
      }
    })
  }

  hide_alert() {
    // console.log('')
    this.exampleVisibility = false;
    // this.renderer2.setStyle(this.el.nativeElement.parentNode, 'display', 'none');
  }

}